<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn
        depressed
        class="mr-3"
        v-on="on"
        color="success">
        <v-icon class="mr-3">mdi-upload</v-icon>
        upload ptn
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Upload PTN
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6">
          <div>
            Silahkan upload data dari excel sesuai template yang sudah disediakan, jika belum ada silahkan download templatenya 
            <a
              target="_blank"
              href="https://drive.google.com/open?id=12NBX_iSqqvZJPBG-ZtjLBZdu8ftUEXaB"
            >
              disini.
            </a>
          </div>
          <v-file-input
            :loading="isLoading"
            accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
            show-size
            counter
            :rules="requiredRule"
            @change="importingData"
            label="File excel template"
          ></v-file-input>

          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import XLSX from 'xlsx'
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  import axios from 'axios'

  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        requiredRule: [v => !!v || 'Data harus diisi'],
        dataSheets: [],
        msg: {
          success: 'Berhasil diupload',
          error: 'Gagal diupload',
          visible: false
        }
      }
    },

    components: { Notif },

    computed: {
      ...mapState('dataPtn', ['isLoading', 'status'])
    },

    methods: {
      importingData (e) {        
        var vm = this
        var reader = new FileReader()
        reader.readAsArrayBuffer(e);
        reader.onload = function(e) {
          var data = new Uint8Array(reader.result);
          var wb = XLSX.read(data, { type: "array" });
          wb.SheetNames.map((sheet, index) => {
            var worksheet = wb.Sheets[wb.SheetNames[index]];
            var dataSheet = XLSX.utils.sheet_to_json(worksheet)
            var dataSiswa = dataSheet.map((sheet, i) => {
              delete sheet.NO
              vm.dataSheets[i] = sheet
            })
          })
          
          // console.log(vm.dataSheets)
        }
      },
      validate () {
        if (this.$refs.form.validate()) {
          // let data = JSON.stringify()
          this.$store.dispatch('dataPtn/postDataPtn', this.dataSheets)
          this.$store.dispatch('dataPtn/updateIsLoading', true)
          this.msg.visible = true
          // axios.post('http://progresio.id/api/public/v1/uploadptn', this.dataSheets)
          //   .then(res => console.log(res))
          //   .catch(err => console.log(err))
        }
      },
      visible (val) {
        this.msg.visible = val        
      }
    }
  }
</script>
