<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        v-on="on"
      	icon 
      	color="primary">
				<v-icon small>mdi-pencil</v-icon>
			</v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Edit PTN
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <v-text-field
            v-model="data.nama"
            label="Nama"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-autocomplete
            v-model="data.provinsi.id"
            label="Provinsi"
            :items="detailProvinsi"
            item-text="nama"
            item-value="id"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-autocomplete>       
          <v-row dense>
            <v-col cols="12" md="6" sm="12">
              <v-text-field
                v-model="data.prodi.nama"
                label="Prodi"
                outlined
                dense
                :rules="requiredRule"
                autocomplete="off"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6" sm="12">
              <v-select
                v-model="data.prodi.kelompok"
                :items="detailKelompok"                
                label="Kelompok"
                outlined
                dense
                :rules="requiredRule"
                autocomplete="off"
              ></v-select>
            </v-col>
          </v-row>

          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            color="primary"
            :loading="isLoading"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
  	props: ['data'],

    data () {
      return {
        dialog: false,
        valid: true,
        kelompok: ['SAINTEK', 'SOSHUM'],
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil diedit',
          error: 'Data gagal diedit',
          visible: false
        }
      }
    },

    components: { Notif },

    computed: { 
      ...mapState('dataPtn', ['status', 'isLoading']),
      ...mapState('dataTempat', ['dataProv']),
      detailProvinsi: {
        get() {
          return this.dataProv
        },
        set(val) {
          this.dataProv.filter(prov => prov.id === this.data.provinsi.id)
        }
      },
      detailKelompok: {
        get() {
          return this.kelompok
        },
        set(val) {
          this.kelompok.filter(prov => prov === this.data.prodi.kelompok)
        }
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          let data = {
            id: this.data.id,
            nama: this.data.nama,
            kelompok: this.data.prodi.kelompok,
            kode_prodi: this.data.prodi.kode,
            prodi: this.data.prodi.nama,
            provinsi_id: this.data.provinsi.id
          }
          this.$store.dispatch('dataPtn/putDataPtn', data)
          this.$store.dispatch('dataPtn/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataPtn/updateStatus', null)
      }
    }
  }
</script>
