<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-6"
			:headers="header"
			:items="dataPtn"
			:items-per-page="20"
			hide-default-footer>
	    <template v-slot:item.no="{item}">
	    	{{dataPtn.map(x => x.id).indexOf(item.id)+(10*(page-1))+1}}    	
	    </template>
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<EditPtn :data="item" />
			</template>		
		</v-data-table>

		<v-pagination
			class="mt-3"
			v-model="page"
			:length="lengthPage"
		></v-pagination>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import EditPtn from './EditPtn'
	import Loader from '@/components/Loader'
	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Kode Prodi', value: 'prodi.kode', sortable: false },
				{ text: 'Universitas', value: 'nama', sortable: false },
				{ text: 'Prodi', value: 'prodi.nama', sortable: false },
				{ text: 'Provinsi', value: 'provinsi.nama', sortable: false },
				{ text: 'Website', value: 'website', sortable: false },
				{ text: 'Kelompok', value: 'prodi.kelompok', sortable: false },
				{ text: 'DT', value: 'daya_tampung', sortable: false },
				{ text: 'Portofolio', value: 'portofolio', sortable: false },
				{ text: 'Jurusan', value: 'jurusan', sortable: false },
				{ text: 'Jml Pendaftar', value: 'jumlah_pendaftar', sortable: false },
				{ text: 'Jml Diterima', value: 'jumlah_diterima', sortable: false },
				{ text: 'IK', value: 'ik', sortable: false },
				{ text: 'NRM', value: 'nrm', sortable: false },
				{ text: 'NAS', value: 'nas', sortable: false },
				{ text: 'Mapel', value: 'mapel', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],

			page: 1
		}),

		components: {
			Loader,
			EditPtn
		},

		created() {
			this.$store.dispatch('dataPtn/getDataPtn', this.page)
			this.$store.dispatch('dataTempat/getDataProv')
			this.$store.dispatch('dataPtn/updateIsLoading', true)
		},

		watch: {
			page(val) {
				this.$store.dispatch('dataPtn/getDataPtn', val)
				this.$store.dispatch('dataPtn/updateIsLoading', true)
			}
		},

		computed: {
			...mapState('dataPtn', ['dataPtn', 'isLoading', 'lengthPage'])
		}
	}
</script>