<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        depressed
        v-on="on"
        class="mr-3"
        color="primary">
        <v-icon class="mr-3">mdi-plus-circle</v-icon>
        Tambah data
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Tambah Upload File
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pa-6 pb-0">
        <v-form v-model="valid" ref="form">
          <v-text-field
            v-model="dataUpload.nama"
            label="Nama file"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-select
            v-model="dataUpload.akses"
            :items="items"
            label="Pilihan akses"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>
        </v-form>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="primary"
          text
          @click="dialog = false">
          kembali
        </v-btn>
        <v-btn
          :disabled="!valid"
          color="primary"
          @click.prevent="validate"
          type="submit"
          depressed>
          simpan
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        dataUpload: {
          nama: '',
          akses: '',
          hp: '',
          alamat: '',
          prov: '',
          kab: ''
        },
        items: ['foo', 'bar', 'zee'],
        requiredRule: [v => !!v || 'Data harus diisi']
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          console.log(this.dataUpload)
        }
      }
    }
  }
</script>
